                <b-row id="ecart2" align-h="center" class="font-weight-bold">
                    <b-col><b-button block pill variant="primary" @click="localisationEdit(localisation)">Editer</b-button></b-col>
                    <b-col><b-button block pill variant="info" @click="cancelLocalisation()">Annuler</b-button></b-col>
                    <b-col><b-button block pill variant="danger" @click="localisationDelet(localisation)">Supprimer</b-button></b-col>
