<script>
export default {
    name:'LocalisationDetail',

    data(){
        return {
            localisation: this.$store.getters.getDetailLocalisation
        }
    },

    methods: {
        back(){
            //alert(this.$store.getters.getDetailLocalisation.pays);
            this.$store.commit('back');
        },
        localisationEdit(localisation){
            this.$store.commit('setDetailLocalisation',localisation);
            this.$router.push("/localisation-edit");
        },
        localisationDelet(localisation){
            this.$store.commit('setDetailLocalisation',localisation);
            alert("Cette fonction est désactivée!!")
        },
        cancelLocalisation(){
            this.$store.commit('setDetailLocalisation',"null");
            this.$router.push("/localisation-list");
        }
    }
}
</script>

<style scoped lang="scss">
    #ecart2 {
        margin-top: 2%;
